<template>
  <div id="app">
    <Navigation v-if="!mobile"/>
    <NavigationMobile v-if="mobile"/>
    <div id="intro">
      <img id="intro-logo" src="/logo.png" alt="">
    </div>
    <div id="content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Navigation from './components/Navigation.vue';
import NavigationMobile from './components/NavigationMobile.vue'
import isMobileMixin from './mixins/isMobileMixin.js'
import Nav from './components/Nav.vue'
export default {
  data() {
    return {
      mobile: this.isMobile()
    }
  },
  mixins: [
    isMobileMixin
  ],
  components: {
    Navigation,
    NavigationMobile
  },
  mounted() {
    var vm = this;
    document.title = 'CRS | Home'
    $(window).resize(function() {
      vm.mobile = vm.isMobile();
    });
    setTimeout(function () {
      $("#intro").animate({
        opacity: 0
      }, 500, function(){
        $('#intro').css('display', 'none');
      });
    }, 2000);
    $("#intro").on('click', function(){
      $("#intro").animate({
        opacity: 0
      }, 500, function(){
        $('#intro').css('display', 'none');
      });
    });
  },
  watch: {
    '$route' (to, from) {
      document.title = to.meta.title || 'Christoph Ritter Studio'
    }
  }
}

</script>

