<template>
  <div>
    <div class="navbar-m">
      <div class="logo-m">
        <div class="step-m">S/S</div>
        <div class="step-m"></div>
        <router-link id="logo" class="step-m" to="/">CHRISTOPH RITTER STUDIO</router-link>
        <div class="step-m"></div>
        <div class="step-m">2120</div>
      </div>
      <div @click="handleMenuBtn" class="menu-btn" style="background-image: url(/svg/sprite_60fps.svg)"></div>
    </div>
    <div class="menu">
      <div class="links">
        <router-link class="mobile-link" to="/">Home</router-link>
        <router-link :to="{name: 'collections', params: {id: '0'}}"class="mobile-link">Ipseity</router-link>
        <router-link :to="{name: 'collections', params: {id: '1'}}"class="mobile-link">Jet Runner</router-link>
        <router-link :to="{name: 'collections', params: {id: '2'}}"class="mobile-link">Curating A Disaster I</router-link>
        <router-link :to="{name: 'collections', params: {id: '3'}}"class="mobile-link">Curating A Disaster II</router-link>
        <router-link class="mobile-link" to="/about">About</router-link>
        <router-link class="mobile-link" to="/collaborations">Collaborations</router-link>
        <router-link class="mobile-link" to="/press">Press</router-link>
      </div>
    </div>
  </div>

</template>

 <script>
 export default {
   methods: {
     handleMenuBtn() {
       if ($('.menu-btn').hasClass('play')) {
         this.closeMenu();
       } else {
         this.openMenu();
       }
     },
     openMenu() {
       $('.menu-btn').removeClass('play-reverse');
       $('.menu-btn').addClass('play');
       $('.menu').addClass('open');
     },
     closeMenu() {
       $('.menu-btn').removeClass('play');
       $('.menu-btn').addClass('play-reverse');
       $('.menu').removeClass('open');
     },
     handleColorChange() {
       if ($('.menu-btn').hasClass('invert')) {
         $('.menu-btn').removeClass('invert');
         $('.step-m').removeClass('invert');
         $('.menu-btn').addClass('to-invert');
         $('.step-m').addClass('to-invert');
       } else if ($('.menu-btn').hasClass('to-invert')) {
         $('.menu-btn').addClass('invert');
         $('.step-m').addClass('invert');
         $('.menu-btn').removeClass('to-invert');
         $('.step-m').removeClass('to-invert');
       }
     }
   },
   watch: {
     $route (to, from) {
       this.closeMenu();
     }
   }
 }
 </script>

 <style>

 .menu {
   position: fixed;
   top: 0;
   right: -100%;
   width: 100vw;
   height: 100vh;
   background-color: white;
   transition: right .5s ease;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   z-index: 998;
 }

 .links {
   display: flex;
   flex-direction: column;
   height: 60vh;
   align-items: center;
   justify-content: space-around;
 }

 .mobile-link {
   color: black;
   font-size: 1.5em;
 }

 .menu.open {
   right: 0;
 }

 @keyframes play18 {
   0% {
     background-position: 0px 0px;
   }
   100% {
     background-position: -720px 0px;
   }
 }

 @keyframes play18-reverse {
   0% {
     background-position: -720px 0px;
   }
   100% {
     background-position: 0px 0px;
   }
 }
 .menu-btn {
   animation-duration: 300ms;
   animation-timing-function: steps(18);
   width: 40px;
   height: 40px;
   background-repeat: no-repeat;
   animation-fill-mode: forwards;
   cursor: pointer;
   z-index: 999;
   margin-top: 20px;
   margin-right: 20px;
 }
 .menu-btn.play {
   animation-name: play18;
 }
 .menu-btn.play-reverse {
   animation-name: play18-reverse;
 }


.step-m {
  width: 150px;
  height: 15px;
  border: 2px solid black;
  display: flex;
  align-items:center;
  justify-content: center;
  margin-top: -2px;
  font-size: .7em;
}
.step-m:first-child {
  border-top: 0px;
}
.step-m:last-child {
  border-bottom: 0px;
}

.logo-m {
  margin-left: 20px;
  margin-top: 20px;
}

.navbar-m {
  position: fixed;
  grid-area: navbar-m;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  z-index: 999;
}

#menu-btn {

}

.menu-btn.invert {
  filter: invert(1);
  transition: filter .3s ease;
}

.step-m.invert{
  color: white;
  border-color: white;
  transition: color .3s ease, border-color .3s ease;
}

@media (max-width: 576px) {
  .step-m {
    width: 100px;
    height: 10px;
    font-size: .5em;
  }
}
 </style>
