import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'
import Thankyou from './views/Thankyou.vue'
import Press from './views/Press.vue'
import Collections from './views/Collections.vue'
import Collaborations from './views/Collaborations.vue'
import Slideshow from './views/Slideshow.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        title: 'CRS | Home'
      }
    },
    {
      path: '/about',
      name: 'about',
      component: About,
      meta: {
        title: 'CRS | About'
      }
    },
    {
      path: '/thankyou',
      name: 'thankyou',
      component: Thankyou,
      meta: {
        title: 'CRS | About'
      }
    },
    {
      path: '/press',
      name: 'press',
      component: Press,
      meta: {
        title: 'CRS | Press'
      }
    },
    {
      path: '/collections/:id',
      name: 'collections',
      component: Collections,
      meta: {
        title: 'CRS | Collections'
      }
    },
    {
      path: '/collections/:id/detail',
      name: 'collection-detail',
      component: Slideshow
    },
    {
      path: '/collaborations',
      name: 'collaborations',
      component: Collaborations,
      meta: {
        title: 'CRS | Collaborations'
      }
    }
  ],
  mode: 'hash',
  linkActiveClass: 'active'
})
