export default {
  methods: {
    setColors(color) {
      console.log('setting colors');
      console.log('color: ' + color);
      $('#nav-wrap *').css('border-color', color),
      $('#nav-wrap *').css('color', color),
      $('#slideshow h1').css('color', color)
    },
    setMobileNavColors(invert) {
      if (invert) {
        $('.menu-btn').addClass('invert');
        $('.step-m').addClass('invert');
        $('#logo').addClass('invert');
      } else {
        $('.menu-btn').removeClass('invert');
        $('.step-m').removeClass('invert');
        $('#logo').removeClass('invert');
      }
    }
  }
}
