<template lang="html">
    <div class="text">
      <h1>{{collections[this.collection].title}}</h1>
      <p v-html="collections[this.collection].description"></p>
      <p class="small" v-html="collections[this.collection].small"></p>
      <router-link :to="{ name: 'collection-detail', params: {id: this.collection} }" class="to-detail-btn">VIEW</router-link>
    </div>
</template>

<script>
import json from '../data/collections.json'

export default {
  props: ['collection'],
  data() {
    return {
      collections: json
    }
  },
  mounted() {
    this.setColors();
    var vm = this;
    $(window).on('resize', function() {
      vm.setColors();
    });
  },
  methods: {
    setColors() {
      var color = this.collections[this.collection].mobileTextColor;
      if ($(window).width() < 991) {
        $('.text').css('color', color);
        $('.to-detail-btn').css('border-color', color);
        $('.to-detail-btn').css('color', color);
      } else {
        $('.text').css('color', 'black');
        $('.to-detail-btn').css('border-color', 'black');
        $('.to-detail-btn').css('color', 'black');
      }
    }
  },
  watch: {
    $route(to, from) {
      this.setColors();
    }
  }
}
</script>

<style>
  p {
    font-size: 1em;
    font-family: 'Raleway', sans-serif;
    line-height: 1.618;
  }

  h1 {
    margin: 0px;
  }

  .to-detail-btn {
    font-size: 1.5em;
    padding: 10px 20px;
    border: black 4px solid;
    width: 120px;
    text-align: center;
    transition: color .2s ease, background-color .2s ease;

  }



  .text {
    position: relative;
    grid-area: center;
    transition: top .3s ease, opacity .3s ease;
    align-self: center;
    justify-self: center;
    opacity: 0;
    top: 50px;
    display: flex;
    flex-direction: column;
    z-index: 2;
    color: white;
    padding: 20px;
    padding-top: 100px;
  }

  @keyframes text-enter {
    from {
      opacity: 0;
      top: 50px;
    } to {
      opacity: 1;
      top: 0px;
    }
  }

  .small {
    font-size: 15px;
  }

  @media (min-width: 991px) {
    .text {
      grid-area: left;
    }

    h1 {
      font-size: 114px;
      line-height: 90px;
    }

    .to-detail-btn:hover {
      background-color: black;
      color: white !important;
    }
  }

  @media (min-width: 1200px) {
    .text {
      padding-top: 0px;
    }
  }
</style>
